.container {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 10% 1fr 10%;
}

.container > * {
    align-self: center;
    justify-self: center;
}

.side-nav {
    grid-column: 1 / 2;
    grid-row: 1 / 4;
    /*overflow-y: auto;*/

}

.top-nav {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}

.content {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
}

